.contact {
  position: relative;
  top: 100px;
}
.contact-container {
  max-width: 690px;
  width: 100%;
  margin: 0 auto;
  padding-top: 42px;
  padding-bottom: 42px;
  text-align: center;
  background-color: var(--white);
  border: var(--card-border);
  box-shadow: var(--card-shadow);
  border-radius: 20px;
}
.contact-container h2 {
  max-width: 300px;
  margin: 0 auto;
  font: var(--headline-4);
  color: var(--primary-color);
}
.contact-container .btn {
  width: 70%;
  max-width: 250px;
  border-radius: 36px;
  margin-top: 16px;
}

@media screen and (min-width: 768px) {
  .contact-container h2 {
    max-width: 300px;
    margin: 0 auto;
    font: var(--headline-3);
  }
}
